@import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&display=swap");
:root {
  --clr-bright-orange: hsl(31, 77%, 52%);
  --clr-dark-cyan: hsl(184, 100%, 22%);
  --clr-very-dark-cyan: hsl(179, 100%, 13%);
  --clr-transparet-white: hsla(0, 0%, 100%, 0.75);
  --clr-very-light-gray: hsl(0, 0%, 95%);
  --clr-white: hsl(0, 0%, 100%);
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  margin: 0;
  background: var(--clr-very-light-gray);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

ul {
  list-style-type: none;
}

a:link,
a:visited {
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

:root {
  --big-shoulders-display: "Big Shoulders Display", cursive;
  --lexend-deca: "Lexend Deca", sans-serif;
}

body {
  font-family: var(--lexend-deca);
  font-size: 1.5rem;
  color: var(--clr-very-light-gray);
  font-weight: 400;
  line-height: 1.6;
}

h1 {
  font-family: var(--big-shoulders-display);
  font-size: 4rem;
  color: var(--clr-white);
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
}

.cards {
  height: 100vh;
  width: 100vw;
  display: grid;
  place-content: center;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 31rem));
}

.card {
  padding: 4.8rem;
  display: grid;
  row-gap: 2.5rem;
}
.card--orange {
  background-color: var(--clr-bright-orange);
  border-radius: 8px 0 0 8px;
}
.card--darkCyan {
  background-color: var(--clr-dark-cyan);
}
.card--veryDarkCyan {
  background-color: var(--clr-very-dark-cyan);
  border-radius: 0 8px 8px 0;
}
.card__img-box {
  width: 6.4rem;
  height: 4rem;
  margin-bottom: 1rem;
}
.card__text {
  max-width: 21rem;
  margin-bottom: 5.8rem;
}
.card__btn {
  justify-self: start;
}
.card__btn--orange:link, .card__btn--orange:visited {
  color: var(--clr-bright-orange);
}
.card__btn--darkCyan:link, .card__btn--darkCyan:visited {
  color: var(--clr-dark-cyan);
}
.card__btn--veryDarkCyan:link, .card__btn--veryDarkCyan:visited {
  color: var(--clr-very-dark-cyan);
}

@media screen and (max-width: 929px) {
  .card--orange {
    border-radius: 8px 0 0 0px;
  }
  .card--darkCyan {
    border-radius: 0 8px 8px 0;
  }
  .card--veryDarkCyan {
    border-radius: 0 0 8px 8px;
  }
}
@media screen and (max-width: 619px) {
  .card--orange {
    border-radius: 8px 8px 0 0;
  }
  .card--darkCyan {
    border-radius: 0 0px 0px 0;
  }
  .card--veryDarkCyan {
    border-radius: 0 0 8px 8px;
  }
}
.btn {
  padding: 1.2rem 3.15rem;
  font-size: 1.5rem;
  line-height: 1.6;
  text-align: center;
  border-radius: 2.5rem;
  background-color: var(--clr-very-light-gray);
  border: 2px solid var(--clr-very-light-gray);
  text-transform: capitalize;
  transition: all 0.2s linear;
}

.btn:hover {
  background-color: transparent;
  color: currentColor;
}